<template>
    <div class="v-upload-images">
        <div class="v-upload-images__con">
            <div v-for="(img, idx) in imgList" :key="idx" v-if="img.src || img.url" class="v-upload-images__frm">
                <div class="v-upload-images__img" :style="{backgroundImage: 'url(' + img.url + ')', backgroundSize: 'cover'}"></div>
                <div v-if="!img.url" class="v-upload-images__loading">
                    <v-spinner color="#fff" type="fading-circle"></v-spinner>
                </div>
                <div @click.stop="delImage(idx)" class="v-upload-images__del"><img src="../assets/delete-photo.png" alt=""></div>
            </div>
            <div @click="uploadImage" key="addBtn" class="v-upload-images__frm v-upload-images__btn" v-if="imgList.length < max">
                <div class="v-upload-images__img icon"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import vSpinner from './v-spinner/index.js';
    import { uploadImages } from '../js/toon/upload-img.js';

    /**
     * 上传图片组件
     * 以后可加入布局，限定单张上传或者指定多列的布局。
     * 添加lazyTag标识，如果有初始化的图片，产品要求要等图片加载完后，显示添加图标。
     * 默认lazyTag不打开，如果需要此方案，传入true即可
     */
    export default {

        components: { vSpinner },

        props: {
            max: {
                type: Number,
                default: 9
            },
            value: {
                type: Array,
                default: () => []
            },
            desc: {
                type: String,
                default: ''
            },
            classes: {
                type: String,
                default: ''
            },
            uploaded: {
                type: Boolean,
                default: true
            },
            lazyTag: {
                type: Boolean,
                default: true
            }
        },

        data () {
            return {
                showTag: !this.lazyTag,
                imgList: this.value,
                uploadedStatus: this.uploaded
            };
        },

        computed: {

        },

        watch: {
            value () {
                this.imgList = this.value;
                if (this.imgList.length < this.max) {
                    this.showTag = true;
                } else {
                    this.showTag = false;
                }
            },

            lazyTag (val) {
                this.showTag = !this.lazyTag;
            },

            imgList (val) {
                this.$emit('input', val);
            },

            /**
             * 捕获图片上传状态，可根据此标识做提交按钮的限定等动作
             * 父页面可在组件调用时指定回调函数： @handle-uploaded-status="handleUploadedStatus"
             */
            uploadedStatus (val) {
                this.$emit('handle-uploaded-status', val);
            }
        },

        methods: {
            uploadImage () {
                // 数量限制
                if (this.imgList.length >= this.max) {
                    this.$toast(`您最多只能选择${this.max}张`);
                    return;
                }
                let max = this.max - this.imgList.length,
                    option = {
                        'sourceType': 2,  // 相机和相册
                        'count': max     // 最大可选数
                    };
                uploadImages(option, (e) => { // toon返回base64的回调, 用于显示图片正在上传的状态, 避免白屏等待
                    let i, len;
                    if (e === 'cancel') {
                        this.checkStatus(); // 检查状态，返回通知                    this.uploadedStatus = true; // 结束上传
                        return;
                    }
                    this.uploadedStatus = false; // 开始上传
                    for (i = 0, len = e.length;i < len;i++) {
                        e[i].url = ''; // 为了实现vue.js对对象内部属性监听，加上初始化值
                        if (this.imgList.length < this.max) this.imgList.push(e[i]);
                    }
                }, (e) => { // 成功上传七牛后的回调, url回写, 并清除图片上传的状态
                    let i, len;
                    for (i = 0, len = this.imgList.length;i < len;i++) {
                        if (e.id === this.imgList[i].id) {
                            this.imgList[i].url = e.url;
                            break;
                        }
                    }
                    this.checkStatus(); // 检查状态，返回通知                    this.uploadedStatus = true; // 结束上传
                }, (e) => { // 中途错误回调, 清除本次上传的图片信息
                    this.checkStatus(); // 检查状态，返回通知                    this.uploadedStatus = true; // 结束上传
                    let i, len, j, jlen, img;
                    if (!e) { // toon 返回错误
                        // do nothing...
                    } else if (typeof e === 'object') { // 图片list
                        for (i = 0, len = this.imgList.length;i < len;i++) {
                            for (j = 0, jlen = e.length;j < jlen;j++) {
                                if (this.imgList[i].id === e[j].id) {
                                    // 删除数组对象
                                    this.imgList.splice(i, 1);
                                    // 删除dom
                                    img = document.getElementById(e[j].id);
                                    img.parentNode.removeChild(img);
                                }
                            }
                        }
                    } else if (typeof e === 'number') { // 图片id
                        for (i = 0, len = this.imgList.length;i < len;i++) {
                            if (this.imgList[i].id === e) {
                                // 删除数组对象
                                this.imgList.splice(i, 1);
                                // 删除dom
                                img = document.getElementById(e);
                                img.parentNode.removeChild(img);
                            }
                        }
                    }
                });
            },
            checkStatus () {
                let tag = true;
                for (let i = 0, len = this.imgList.length;i < len;i++) {
                    if (!this.imgList[i].url) {
                        tag = false;
                    }
                }
                if (tag) {
                    this.uploadedStatus = true;
                    this.$emit('handle-uploaded-status', true);
                } else {
                    this.uploadedStatus = false;
                    this.$emit('handle-uploaded-status', false);
                }
            },
            delImage (idx) {
                this.imgList.splice(idx, 1);
            },

            /**
             * 图片预览
             */
            goViewer (idx) {
                this.$root.img = [];
                this.imgList.forEach((v, i) => {
                    this.$root.img.push(v.src);
                });
                this.$router.push({name: 'viewer', query: {idx: idx}});
            }
        }
    };
</script>
