    }

    .v-upload-images__del {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        img {
            width: pxtorem(18px);
            height: pxtorem(18px);
        }
    }

    .v-upload-images__tit {
        margin: 0 pxtorem(15px) pxtorem(15px);
        font-size: pxtorem(14px);
        color: #737373;
    }
</style>
